import { groupBy, reduce } from 'lodash'
import { useMemo } from 'react';

import useData from './useData';
import images from './images'

const withData = SubComp => props => {
  const { match: { params: { name } } } = props
  const allData = useData()
  const data = useMemo(() => {
    if (!allData) return null
    const grouped = groupBy(allData, 'name')
    const ordered = grouped[name].map((cfg, index) => ({
      ...cfg,
      index,
      order: cfg.order - 1,
      centeroid: [
        cfg.x * 1 + cfg.width / 2,
        cfg.y * 1 + cfg.height / 2,
      ],
    }))
    const positions = []
    const range = ordered.reduce((all, cfg) => {
      const top = cfg.y * 1
      const bottom = top + cfg.height * 1
      return [Math.min(all[0], top), Math.max(all[1], bottom)]
    }, [Infinity, 0])
    const centeroid = range[0] + range[1] / 2
    const totalHeight = reduce(groupBy(ordered, 'order'), (total, orderEle, order) => {
      const y = total + orderEle[0].height * 1
      positions[order] = y
      return y
    }, 0)
    return {
      totalHeight,
      centeroid,
      positions,
      imgs: ordered.map(o => ({
        ...o,
        src: images[name][o.layerName],
      })),
    }
  }, [allData, name])
  return data ? <SubComp {...props} data={data} /> : null
}

export default withData
