import React, { useEffect, useRef, useState, useMemo, createRef } from 'react'
import { AspectRatio } from '@chakra-ui/react'
import ReactFullpage from '@fullpage/react-fullpage';
import { range } from 'lodash'
import gsap from 'gsap'

import FullpageHeight from '../../components/FullpageHeight'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Container from '../../components/Container'
import BackgroundImage from '../../components/BackgroundImage'
import withData from './data/withData';
import animations from './data/animations'

const imgSize = [850, 624]
const pageTransition = 1000

let fpApi
let timeline

const TrashPage = ({ match: { params: { name } }, data }) => {
  const trashContainerRef = useRef()
  const layerRefs = useMemo(() => data.imgs.map(() => createRef()), [data])
  const [currentPage, setCurrentPage] = useState(0)
  useEffect(() => {
    if (fpApi) {
      fpApi.silentMoveTo(0)
    }
    if (timeline) {
      timeline.kill()
    }
    const animation = animations[name]
    gsap.set(trashContainerRef.current, { x: '-50%', y: '-50%' })
    timeline = gsap.timeline()
    timeline.to(trashContainerRef.current, { x: '-75%', duration: 1 })
    const yStart = data.totalHeight - (data.totalHeight - imgSize[1]) / 2
    data.imgs.forEach((cfg) => {
      gsap.set(layerRefs[cfg.index].current, { y: '0%' })
      timeline.to(layerRefs[cfg.index].current, {
        y: `${(yStart - cfg.y - data.positions[cfg.order]) / imgSize[1] * 100}%`,
        duration: pageTransition / 1000,
      }, 0)
      if (animation && animation[cfg.layerName]) {
        Object.entries(animation[cfg.layerName]).forEach(([d, ani]) => {
          timeline.to(layerRefs[cfg.index].current, {
            ...ani,
            duration: pageTransition / 1000 * (d / 100),
          }, 0)
        })
      }
    })

  }, [name, data])
  useEffect(() => {
    // console.log(currentPage)
    timeline.tweenTo(currentPage * pageTransition / 1000)
  }, [currentPage])
  const isExploded = currentPage === 1
  return (
    <Container>
      <Box.Relative>
        <ReactFullpage
          licenseKey="6313A8FC-72634A8F-B83D01C8-ACBFD3D5"
          scrollingSpeed={pageTransition}
          onLeave={(origin, destination) => setCurrentPage(destination.index)}
          render={({ fullpageApi }) => {
            fpApi = fullpageApi
            return (
              <ReactFullpage.Wrapper>
                {range(2).map(i => (
                  <div key={i} className="section">
                    <Flex mt="-4em">
                      <Box width="50%"></Box>
                      <Box width="50%" pl="10%">
                        {!i && <Text.H3 fontWeight="bold">{name}</Text.H3>}
                      </Box>
                    </Flex>
                  </div>
                ))}
              </ReactFullpage.Wrapper>
            )
          }}
        />
        {data && (
          <FullpageHeight position="absolute" top="0" left="0" right="0" pointerEvents="none">
            <Box.Absolute
              top="50%"
              left="50%"
              width="50%"
              ref={trashContainerRef}
              overflow="visible"
            >
              <AspectRatio ratio={imgSize[0] / imgSize[1]} overflow="visible">
                <Box overflow="visible">
                  {data.imgs.map(({ src, centeroid, x, width, layerName }, i) => {
                    const left = (x * 1 + width * 1) / imgSize[0] * 100 + 3
                    const top = `${centeroid[1] / imgSize[1] * 100}%`
                    return (
                      <Box.FullAbs ref={layerRefs[i]} key={i}>
                        <BackgroundImage ratio={imgSize[0] / imgSize[1]} src={src} />
                        <Box
                          opacity={isExploded * 1}
                          transition="opacity 1s ease"
                        >
                          {/* <Box.Absolute
                            top={top}
                            left={`${left}%`}
                            width="0.5em"
                            height="0.5em"
                            transform="translate(-50%, -50%)"
                            bg="black"
                          /> */}
                          <Box.Absolute
                            left={`${left}%`}
                            top={top}
                            style={{
                              right: isExploded ? '2%' : `${100 - left}%`,
                            }}
                            height="1px"
                            bg="black"
                            transition="all 1.5s ease"
                          />
                          <Box.Absolute
                            top={top}
                            style={{ left: isExploded ? '100%' : `${left}%` }}
                            transform="translateY(-50%)"
                            transition="all 1.5s ease"
                            whiteSpace="nowrap"
                          >
                            {layerName}
                          </Box.Absolute>
                        </Box>
                      </Box.FullAbs>
                    )
                  })}
                </Box>
              </AspectRatio>
            </Box.Absolute>
          </FullpageHeight>
        )}
      </Box.Relative>
    </Container>
  )
}

export default withData(TrashPage)
