import { useEffect, useState } from 'react'
import { tsv } from 'd3-fetch'
import { mapKeys } from 'lodash'

import source from './cfg.tsv'

const keys = {
  '垃圾名稱': 'name',
  '拆解排序': 'order',
  '圖層H高度': 'height',
  '圖層W寬度': 'width',
  '圖層Y位置': 'y',
  '圖層X位置': 'x',
  '圖層名稱': 'layerName',
}

const useData = () => {
  const [data, setData] = useState()
  useEffect(() => {
    tsv(source).then(d => {
      return setData(d.map(dd => mapKeys(dd, (v, k) => keys[k] || k)))
    })
  }, [])
  return data
}

export default useData
